.questions-list {
    padding: 16px;
    max-width: 600px;
    margin: auto;
    background: #f9f9f9;
    border-radius: 8px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    padding: 10px;
    margin: 5px 0;
    background: white;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    word-wrap: break-word;
  }
  
  .new-question {
    background-color: #e3f2fd;
  }
  
  .question-text {
    flex-grow: 1;
    text-align: justify;
    white-space: pre-line; /* ✅ This ensures new lines (\n) are rendered */
    padding-left: 10px;
  }
  
  .question-date {
    font-size: 0.8em;
    color: gray;
  }
  .full-width {
    width: 100%; /* Makes mat-form-field take the full width of the mat-card */
  }
  /* Ensure the font is applied to the selected text in mat-select */
mat-form-field, mat-select, mat-option, .mat-select-panel {
  font-family: 'Vazirmatn', sans-serif !important;
}

/* Override Material's dropdown options */
.mat-select-value, .mat-option-text {
  font-family: 'Vazirmatn', sans-serif !important;
}

/* Make sure the dropdown panel uses the font */
.mat-select-panel {
  font-family: 'Vazirmatn', sans-serif !important;
}
span.mat-mdc-select-placeholder.mat-mdc-select-min-line{
  font-family: 'Vazirmatn', sans-serif !important;
}
mat-label {
  font-family: 'Vazirmatn', sans-serif !important;
}
::ng-deep span.mdc-button__label{
  font-family: 'Vazirmatn', sans-serif !important;
}

.button-group {
  display: flex;
  justify-content: space-between; /* First button sticks to start, second to end */
  align-items: center;
  width: 100%; /* Ensures the buttons take the full width of the parent */
  margin-bottom: 10px; /* Optional spacing below buttons */
}
